/*import Link from 'next/link'*/

import slug from '../helpers/slug'

export default class PodcastList extends React.Component{
  render(){

    const { audioClips, onClickPodcast } = this.props

    return(
      <section>
      { audioClips.map((clip) => (
          <a href={`/${slug(clip.channel.title)}.${clip.channel.id}/${slug(clip.title)}.${clip.id}`}
            key={clip.id}
            className='podcast'
            onClick={ (event) => onClickPodcast(event,clip) }
          >
            <h3>{ clip.title }</h3>
            <div className='meta'>
              { Math.ceil(clip.duration / 60) } minutes
            </div>
          </a>
        ))}
        <style jsx>{`
          .podcast {
            display: block;
            text-decoration: none;
            color: #333;
            padding: 15px;
            border-bottom: 1px solid rgba(0,0,0,0.2);
            cursor: pointer;
          }
          .podcast:hover {
            color: #000;
          }
          .podcast h3 {
            margin: 0;
          }
          .podcast .meta {
            color: #666;
            margin-top: 0.5em;
            font-size: 0.8em;
          }
        `}</style>
      </section>
    )
  }
}


